@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
body{
  background: #f4c4f3;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#fc67fa), to(#f4c4f3));
  background: linear-gradient(to right, #fc67fa, #f4c4f3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  font-family: 'Bree Serif', serif;
}

.homeLogin {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
div p {
  font-size: 26px;
}

p :nth-child(1) {
  color: brown;
}
.main {
  display: flex;
  justify-content: center;
}

blockquote {
  background-color: aqua;
  display: flex;
  justify-content: center;
  border-radius: 2px;
}

.card {
  background-color: inherit;
  border-color: black;
  border-radius: 5px;
  width: 500px;
  padding: 5px;
}

ul li {
  margin-bottom: 10px;
}

.nextBtn {
  width: 150px;
  border-radius: 20px;
}
.timer {
  font-family: 'Bree Serif', serif;
  font-size: larger;
  color: red;
}
.loginForm {
  margin-top: 15px;
}

.fieldset {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px;
}
.fieldset button {
  margin: 2px;
  width: 87px;
  height: 40px;
}
.feedback {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.feedback .message {
  flex-direction: column;
}

.buttons {
  display: flex;
  justify-content: center;
}

h5 :nth-child(1) {
  color: brown;
}

span {
  font-weight: 900;
}
